import React from 'react';
import './MovieRow.css'
import MovieRowCard from './MovieRowCard'

import NavigateBeforeIcon from '@mui/icons-material/NavigateBefore';
import NavigateNextIcon from '@mui/icons-material/NavigateNext';

import Tmdb from '../tmdb.js';

const MovieRow = ({title,items})=>{
	const img='https://image.tmdb.org/t/p/w300'

	const [moving,setMoving] = React.useState(0)
	const [idTime,setIdTime] = React.useState(0)
	const [trailerMovie,setTrailerMovie] = React.useState()
	const [dataTrailerMovie,setDataTrailerMovie] = React.useState()
	const [positionTrailer,setpositionTrailer] = React.useState({})
	const [op,setOp] = React.useState({w:'0px',h:'0px'})
	const [aparece,setAparece] = React.useState({h:0,p:0})

	const handleLeftArrow = ()=>{
		moving + Math.round(window.innerWidth / 2) > 0 ? setMoving(0) : setMoving(moving + Math.round(window.innerWidth / 2))
	}
	const handleRightArrow = ()=>{
		const largoItems=items.results.length *285
		console.log(moving,window.innerWidth,largoItems)

		moving - (Math.round(window.innerWidth)) > -largoItems ?
		setMoving(moving - (Math.round(window.innerWidth/2)) )
		:
		setMoving(-largoItems + Math.round(window.innerWidth) - 380) 
	}



	const getTrailer = async(items,e)=>{
		const {id,video} = items
		const result = window.setTimeout(async ()=>{
			const dataVideo = await Tmdb.getFilmAllMovies(id, video !== undefined ? 'movie': 'tv')
			setTrailerMovie(dataVideo)
			const dataInfo = await Tmdb.getMovieFilm(id,video !== undefined ? 'movie': 'tv')
			let year = dataInfo?.first_air_date ? new Date(dataInfo?.first_air_date).getFullYear() : new Date(dataInfo?.release_date).getFullYear()
			let genre = dataInfo.genres.map((el,key)=> el.name);
			let create = dataInfo.created_by?.map((el,key)=> el.name);
			setDataTrailerMovie({...dataInfo,genre,create,year})
			setOp({w:'560px',h:'490px'})
		},500)
		setIdTime(result)
		//saco los padres de las imagenes para conocer su verdadera posicion
		if (window.innerWidth - e.target.offsetParent.offsetLeft < 500) {
			setpositionTrailer({screenX:e.target.offsetParent.offsetLeft,screenY:e.target.offsetParent.offsetTop+50})
			window.setTimeout(()=>{
				setpositionTrailer({screenX:e.target.offsetParent.offsetLeft-230,screenY:e.target.offsetParent.offsetTop-50})
			},500)		
		}else if(e.target.offsetParent.offsetLeft === 30){
			setpositionTrailer({screenX:e.target.offsetParent.offsetLeft+100,screenY:e.target.offsetParent.offsetTop+50})
			window.setTimeout(()=>{
				setpositionTrailer({screenX:e.target.offsetParent.offsetLeft,screenY:e.target.offsetParent.offsetTop-50})

			},500)	
		}	
		else{
			setpositionTrailer({screenX:e.target.offsetParent.offsetLeft+100,screenY:e.target.offsetParent.offsetTop+50})
			window.setTimeout(()=>{
				setpositionTrailer({screenX:e.target.offsetParent.offsetLeft-130,screenY:e.target.offsetParent.offsetTop-50})

			},500)	
		}	

	}

	const clearTime = async()=>{
		window.clearTimeout(idTime)
	}

	const clearData = ()=>{
		setTrailerMovie()
		setOp({w:'0px',h:'0px'})
		setAparece({h:0,p:0})
	}

	return (
		<div className="movieRow">
			<h2>{title}</h2>
			
			<MovieRowCard setIdTime={setIdTime} idtime={idTime} clearTime={clearTime}positionTrailer={positionTrailer} clearData={clearData} trailerMovie={trailerMovie} op={op} setOp={setOp} aparece={aparece} setAparece={setAparece} dataTrailerMovie={dataTrailerMovie}/>

			<div className="movieRow--left" onClick={handleLeftArrow}>
				<NavigateBeforeIcon style={{fontSize:50}}/>
			</div>
			<div className="movieRow--right" onClick={handleRightArrow}>
				<NavigateNextIcon style={{fontSize:50}}/>
			</div>

			<div className="movieRow--listarea">
				<div className="movieRow--list" style={{marginLeft:`${moving}px`,transition:'margin ease 0.5s',width:items.results.length * 300}}>
					{items.results.length > 0 && items.results.map((items,key)=>
					items.poster_path? (
						<div key={key}  className="movieRow--item" >

							{title === 'Original de netflix' &&
								<img src="https://cdn-icons-png.flaticon.com/512/732/732228.png" alt="" className="movieRow--item--img--net"/>
							}
							{
							 items.backdrop_path ?
								<img src={`${img}${items.backdrop_path}`} alt={items.original_title} onMouseEnter={e=>getTrailer(items,e)} onMouseLeave={e=>clearTime()}/>:
								<img src="https://cdn-icons-png.flaticon.com/512/732/732228.png" alt="" style={{height:'160px',width: '300px',}} onMouseEnter={e=>getTrailer(items,e)} onMouseLeave={e=>clearTime()} />
							}
						</div>
						):''
				)}
				</div>
				
			</div>
		</div>
		)
}


export default MovieRow


